/* eslint-disable no-nested-ternary */
/* eslint-disable import/no-cycle */
/* eslint-disable react/jsx-boolean-value */
/* eslint-disable react/no-unstable-nested-components */
import { getDataSiteList,getWpId,getOrderType,getOrderTypeId } from '@app/store/action/siteListDeliveryRequestAction';
import React,{useEffect,useState} from 'react'
import { useDispatch,useSelector } from 'react-redux'
import {Select,Upload,message,Form,Modal,Table, Input,Menu, Dropdown, Button, Space, Spin, Row, Col,Tooltip  } from 'antd'
import { CloseSquareTwoTone ,CloseSquareOutlined,CalendarTwoTone,UserAddOutlined, EditOutlined,DeleteOutlined,SearchOutlined,CheckCircleFilled,MoreOutlined,DeleteTwoTone,UploadOutlined } from '@ant-design/icons'
import { useHistory } from 'react-router-dom';
import API  from '../../utils/apiServices';
import {IconButton, TextField}  from '@mui/material/';
import Search from '@app/components/searchcolumn/SearchColumn';
import moment from 'moment';
import CircularProgress from '@mui/material/CircularProgress';
import SimCardDownloadIcon from '@mui/icons-material/SimCardDownload';
import exportFromJSON from 'export-from-json'

const TableInboundUpload = () => {
    const { Option } = Select;
    const [isLoading, setIsLoading] = useState(true);
    const [inboundUploadFile,setInboundUploadFile] = useState([]);
    const [isReviseFile, setIsReviseFile] = useState(false);
    const [isReschedule, setIsReschedule] = useState(false);
    const [isCancelTask, setIsCancelTask] = useState(false);
    const [selectedSconId, setSelectedSconId] = useState('');
    const [selectedWPID, setSelectedWPID] = useState('');
    const [lspName, setLspName] = useState('');
    const [selectedOrderDetailId, setSelectedOrderDetailId] = useState('');
    const [selectedTaskScheduleId, setSelectedTaskScheduleId] = useState('');
    const [selectedTransDelegateId, setSelectedTransDelegateId] = useState('');
    const [pickupDate, setPickupDate] = useState('');
    const [cancelLoading, setCancelLoading] = useState(false);
    const user = useSelector((state) => state.auth.user);
    const [errorLog,setErrorLog] = useState([])
    const [fileUpload, setFileUpload] = useState(null);
    const [uploading, setUploading] = useState(false);
    const [inbFileId, setInbFileId] = useState(0);

    const props = {
        onRemove: () => {
            setFileUpload(null);
            return fileUpload
        },
        beforeUpload: file => {
            setFileUpload(file);
            return false;
        },
        fileUpload,
    };


    const getInboundUploadFileList = () => {
        setIsLoading(true);
        API.getInboundUploadFile().then(
            result=>{
                setInboundUploadFile(result);
                setIsLoading(false);
                console.log("scontaskpendnig",result);
            }
        )
    }

    function getErrorLog(id,fileNames){

        API.getInboundErrorList(id).then(
            result=>{
                console.log('i am error log Scope',result)
                
                setErrorLog(result);
                const data = result;
                //const data = result.map((rs)=>CreateDataPOScope.errorLog(rs.workpackageID , rs.phase, rs.packageName, rs.region, rs.dataStatus))
                const exportType =  exportFromJSON.types.xls;
                const fileName = `errorlog_${fileNames}`;
                exportFromJSON({ data, fileName, exportType });
            }
        )
    }

    const handleUploadFile = (id) =>
    {
        setInbFileId(id);
        console.log("inbfileid: ", id);
        message.success(id);
        setIsReviseFile(true);
    }
    const handleDeleteFile = (id) =>
    {
        setInbFileId(id);
        console.log("inbfileid: ", id);
        message.success(id);
        setIsReviseFile(true);
    }
    const handleOkReviseFile = () =>
    {
        
        setIsReviseFile(false);
    }
    const handleCancelReviseFile = () =>
    {
        
        setIsReviseFile(false);
    }

    const columns = [
        {
            title : "No",
            width : 50,
            render: (value, item, index) => 1 + index
        },
        {
            title : "File Name",
            dataIndex:'fileName',
            ...Search('fileName'),
        },
        {
            title : "Upload Date",
            dataIndex:'uploadedDate',
            width: 120,
            render:(record)=>{
                return (
                    <Space>
                        <p>{moment(record.uploadedDate).format("YYYY-MM-DD")}</p>
                    </Space>
                )
            },
            ...Search('uploadedDate'),
        },
        {
            title : "Upload By",
            dataIndex:'uploadedBy',
            width: 150,
            ...Search('uploadedBy'),
        },
        {
            title : "Status",
            dataIndex:'executeStatus',
            width: 100,
            ...Search('executeStatus'),
        },
        {
            title:"Err Message",
            key:"orderMaterialId",
            align:'center',
            fixed: 'right',
            width: 100,
            render:(record)=>{
                return (
                    <Space>
                        {!record.rowlogCount > 0 ?
                            null
                            :
                            <Tooltip title="Download Log">
                                <IconButton
                                    aria-label="expand row"
                                    size="small"
                                    color="error"
                                    onClick={() => getErrorLog(record.inbFileId,record.fileName)}
                                >
                                    <SimCardDownloadIcon />
                                </IconButton>
                            </Tooltip>
                        }
                    </Space>
                )
            }
            
        },
        {
            title:"Action",
            key:"orderMaterialId",
            align:'center',
            fixed: 'right',
            width: 100,
            render:(record)=>{
                return (
                    <Space>
                        <Tooltip title="Assign Task">
                            <IconButton 
                                size='small' 
                                color="primary" 
                                aria-label="upload file"
                                component="span"
                                onClick={()=>handleUploadFile(record.inbFileId)}>
                                <UploadOutlined />
                            </IconButton>
                        </Tooltip>
                        
                        <Tooltip title="Cancel Task">
                            <DeleteTwoTone  twoToneColor="#FF0000" onClick={() => handleDeleteFile(record)} />
                        </Tooltip>
                    </Space>
                )
            }
            
        },
        
    
    ]

    const handleUpload = () => {
        setUploading(true)
        
        API.postReviseInboundFile(inbFileId,fileUpload).then(
            result=>{
                if(result.value.status=="success"){
                    setFileUpload(null);
                    setUploading(false);
                    props.onRemove();
                    // window.location.reload()
                    message.success('upload successfully.');
                }
                else{
                    setFileUpload(null);
                    setUploading(false);
                    props.onRemove();
                    message.error(result.value.message);
                }

                console.log('i am PO upload xx',result)
            }
        );
    }

    useEffect(() => {
        getInboundUploadFileList();
    },[])

    return(
        isLoading ?   
            <Row justify="center">
                <Col span={1}>    
                    <Spin />
                </Col>
            </Row>  
            :
            <><Table
                scroll={{ x: '100%' }}
                size="small"
                // expandable={{ expandedRowRender }}
                columns={columns}
                dataSource={inboundUploadFile}
                pagination={{
                    pageSizeOptions: ['5', '10', '20', '30', '40'],
                    showSizeChanger: true,
                    position: ["bottomLeft"],
                }}
                bordered />
            <Modal title="Upload Revise File"
                visible={isReviseFile}
                onOk={handleOkReviseFile}
                onCancel={handleCancelReviseFile}
                footer={null}
                destroyOnClose={true}
            >
                <div>
                    <p>{inbFileId}</p>
                    <Upload {...props}>
                        <Button icon={<UploadOutlined />}>Select File</Button>
                    </Upload>
                    <Button
                        type="primary"
                        onClick={handleUpload}
                        disabled={fileUpload == null}
                        loading={uploading}
                        style={{ marginTop: 16 }}
                    >
                        {uploading ? 'Uploading' : 'Start Upload'}
                    </Button>
                </div>
            </Modal>
            </>
            
    )
}

export default TableInboundUpload;